header {
  justify-content: space-between;
  margin: auto;
  padding: 2em 0;
  max-width: 1240px;
  width: 100%;
  img {
    max-width: 20%;
    height: 20%;
  }
  nav {
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    min-width: 20%;
    gap: 1em;
  }
}
