.gallery {
  justify-content: center;
  flex-wrap: wrap;
  gap: 2em;
  padding: 3em;
  width: 100%;
  border-radius: 25px;
  background-color: $background-color;
  article {
    cursor: pointer;
    position: relative;
    max-width: 30%;
    height: 22em;
    overflow: hidden;
    img {
      max-width: 100%;
      height: 22em;
      border-radius: 10px;
      object-fit: cover;
    }
    .gradient {
      position: absolute;
      display: flex;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
      h3 {
        align-self: flex-end;
        width: 65%;
        margin: 1em;
        color: white;
      }
    }
  }
}
